import Vue from 'vue'
import Router from 'vue-router'
import Layout from '@/pages/Layout'

import Home from '@/pages/Home'
import User from '@/pages/User'
import Cart from '@/pages/Cart'
import Cate from '@/pages/Cate'
import GoodsList from '@/pages/GoodsList'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'layout',
      component: Layout,
      redirect:'/home',
      children:[
        {
          path:'/home',
          component:Home,
          meta:{
            title:'首页'
          }
        },
        {
          path:'/cate',
          component:Cate,
          meta:{
            title:'商品分类'
          }
        },
        {
          path:'/cart',
          component:Cart,
          meta:{
            title:'购物车'
          }
        },
        {
          path:'/user',
          component:User,
          meta:{
            title:'我的'
          }
        },
        {
          path:'/goodslist/:id',
          component:GoodsList,
          meta:{
            title:'商品列表'
          }
        }
      ]
    }
  ]
})



