<template>
  <div>
    <!-- 页头 -->
    <van-nav-bar title="首页" />
    <!-- 轮播图区域 -->
    <van-swipe class="my-swipe" :autoplay="1500" indicator-color="black">
      <van-swipe-item v-for="item in 6" :key="item">
          <img :src="`https://img.yzcdn.cn/vant/apple-${item}.jpg`" alt="">
      </van-swipe-item>
    </van-swipe>

    <!-- tab栏切换 -->
    <van-tabs v-model="activeTab">
      <van-tab title="热门推荐">
        <van-card
          v-for="item in 10"
          :key="item"
          tag="hot"
          price="2.00"
          desc="描述信息"
          title="商品标题"
          thumb="https://img.yzcdn.cn/vant/ipad.jpeg"
        >
          <template #footer>
            <van-button type="primary" icon="cart-o" size="small"></van-button>
          </template>
        </van-card>
      </van-tab>
      <van-tab title="发现新品">
        <van-card
          v-for="item in 10"
          :key="item"
          tag="new"
          price="2.00"
          desc="描述信息"
          title="商品标题"
          thumb="https://img.yzcdn.cn/vant/ipad.jpeg"
        >
          <template #footer>
            <van-button type="primary" icon="cart-o" size="small"></van-button>
          </template>
        </van-card>
      </van-tab>
      <van-tab title="所有商品">
        <van-card
          v-for="item in 10"
          :key="item"
          price="2.00"
          desc="描述信息"
          title="商品标题"
          thumb="https://img.yzcdn.cn/vant/ipad.jpeg"
        >
          <template #footer>
            <van-button type="primary" icon="cart-o" size="small"></van-button>
          </template>
        </van-card>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
export default {
  data(){
      return {
          activeTab: 0
      }
  }
};
</script>

<style scoped>
.my-swipe .van-swipe-item {
    height: 220px;
}
.my-swipe .van-swipe-item img{
    width: 100%;
    /* height: 100%; */
}
/* tabs */
.van-tabs{
    margin-bottom: 60px;
}
</style>


