<template>
  <div class="cart-container">
    <!-- 页头 -->
    <van-nav-bar left-arrow title="购物车"></van-nav-bar>
    <!-- 购物车列表 -->
    <van-card
      v-for="item in 4"
      :key="item"
      price="2.00"
      desc="描述信息"
      title="商品标题"
      thumb="https://img.yzcdn.cn/vant/ipad.jpeg"
    >
      <template #footer>
       <van-stepper v-model="value" theme="round" button-size="22" disable-input />
      </template>
    </van-card>

    <!-- 提交订单 -->
    <van-submit-bar :price="3050" button-text="提交订单" @submit="onSubmit" />
  </div>
</template>

<script>
export default {    
    data(){
        return {
            value:1
        }
    },
    methods:{
      onSubmit(){
        // this.$notify({ type: 'success', message: '订单提交中...' });
          this.$notify('提示文案');
      }
    }
};
</script>

<style>
.cart-container{
  margin-bottom: 120px;
}
.van-submit-bar{
  bottom:50px!important;
}
</style>


