<template>
  <div>
    <!-- 二级路由出口 -->
    <router-view></router-view>
    <!-- tabbar导航 -->
    <van-tabbar route v-model="active" active-color="#07c160" inactive-color="#000">
      <van-tabbar-item to="/home" name="home" icon="home-o">首页</van-tabbar-item>
      <van-tabbar-item to="/cate" name="cate" icon="apps-o">分类</van-tabbar-item>
      <van-tabbar-item to="/cart" name="cart" icon="cart-o">购物车</van-tabbar-item>
      <van-tabbar-item to="/user" name="user" icon="user-o">我的</van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
export default {
  data() {
    return {
      active: "home"
    };
  }
};
</script>

<style>
</style>


