<template>
  <div class="cate-container">
    <!-- 页头 -->
    <van-nav-bar left-arrow @click-left="$router.go(-1)" title="分类"></van-nav-bar>
    <!-- 侧边导航栏 -->
    <van-sidebar v-model="activeKey" @change="onChange">
      <van-sidebar-item
        :title="item.catename"
        :value="item.id"
        v-for="item in cateList"
        :key="item.id"
      />
    </van-sidebar>
    <!-- 主体内容 -->
    <div class="cate-list">
      <van-grid :border="false" :column-num="3">
        <van-grid-item @click="$router.push('/goodslist/'+item.id)" v-for="item in secondCateList" :key="item.id">
          <van-image :src="$imgBaseUrl+item.img" />
          <h6>{{item.catename}}</h6>
        </van-grid-item>
      </van-grid>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      activeKey: 0,
      cateList: [],
      secondCateList: []
    };
  },
  methods: {
    onChange(index) {
      this.secondCateList = this.cateList[index].children;
      console.log(this.secondCateList);
    },
    getCateList() {
      this.$http.get("/getcate").then(data => {
        if (data.code === 200) {
          this.cateList = data.list;
          this.onChange(0);
        }
      });
    }
  },
  created() {
    this.getCateList();
  }
};
</script>

<style>
.cate-container {
  position: relative;
  /* background: #eee; */
}
.cate-list {
  position: absolute;
  left: 80px;
  top: 46px;
}
.van-image img {
  width: 100%;
  height: 100%;
}
</style>


